.App {
  text-align: center;
}
*{
  font-size: 20px;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.btngrp{
  padding: 20x;
}
.btn{
  padding: 20px;
  border: none;
  color:black;
  background-color: whitesmoke;
  border-radius: 5px;
  box-shadow: 0 3px 4px #ccc;
  margin: 20px;
}
.btn:hover{
  opacity: 0.8;
  font-size: 1.2em;
  background-color:black;
  color:white;
}
.btn:active{
  background-color: aquamarine;
}
.formgrp{
  padding: 20px;
  background-color: whitesmoke;
  width: min(800px,80%);
  align-items: center;
  margin: auto;
  box-shadow: 0 3px 4px #ccc;
  border-radius: 20px;
  margin-top: 20px;
}
.formelement{
  padding: 20px;
  margin: 20px;
  border-radius: 20px;
  font-weight: 800;
}
.subbtn{
  background-color: #61dafb;
  border: none;

  color:white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 6px 8px #ccc;
}
.subbtn:hover{
  background-color: black;
  opacity: 0.8;
  font-size: 1.2em;
 
}
.subbtn:active{
  color:#61dafb;
  box-shadow: none;
}
.row{
  background-color: whitesmoke;
  box-shadow: 0 3px 6px #ccc;
  padding: 20px;
  font-size: 20px;
  font-weight: 800;
  width: min(500px,80%);
  margin: auto;
  margin-top: 50px;
}
.Title{
  font-weight: 800;
  font-size: 30px;
  padding: 20px;
}